* {
  padding: 0;
  margin: 0;
}

/* 
  GENERAL
*/
@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans&display=swap');

body {
  font-family: 'Open Sans' ,sans-serif;
}

button:focus {
  outline: none !important;
}

/* 
  JUMBOTRON
*/

.jumbotron {
  font-family: 'Montserrat', sans-serif;
}

/* 
  BUTTONS
*/ 
button {
  border: none;
  background-color: #fff;
  margin: 0 5px;
}